<template><div class="markdown-body"><h1>选择控件</h1>
<p>选择控件允许用户选择的选项，分为 <code v-pre="">mu-checkbox</code> ，<code v-pre="">mu-radio</code> 和 <code v-pre="">mu-switch</code>。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-checkbox v-model=\&quot;normal.checkbox\&quot; :label=\&quot;'Checkbox: ' + normal.checkbox\&quot;&gt;&lt;/mu-checkbox&gt;\n  &lt;/mu-flex&gt;\n  &lt;div class=\&quot;select-control-group\&quot;&gt;\n    &lt;mu-flex class=\&quot;select-control-row\&quot; :key=\&quot;'radio ' + i\&quot; v-for=\&quot;i in 3\&quot;&gt;\n      &lt;mu-radio :value=\&quot;i\&quot; v-model=\&quot;normal.radio\&quot; :label=\&quot;'Radio ' + i\&quot;&gt;&lt;/mu-radio&gt;\n    &lt;/mu-flex&gt;\n  &lt;/div&gt;\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-switch v-model=\&quot;normal.switch\&quot; :label=\&quot;'Switch: ' + normal.switch\&quot;&gt;&lt;/mu-switch&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      normal: {\n        checkbox: true,\n        radio: 1,\n        switch: false\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.select-control-row{\n  padding: 8px 0;\n}\n.select-control-group {\n  margin: 16px 0;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex class="select-control-row">
    <mu-checkbox v-model="normal.checkbox" :label="'Checkbox: ' + normal.checkbox"></mu-checkbox>
  </mu-flex>
  <div class="select-control-group">
    <mu-flex class="select-control-row" :key="'radio ' + i" v-for="i in 3">
      <mu-radio :value="i" v-model="normal.radio" :label="'Radio ' + i"></mu-radio>
    </mu-flex>
  </div>
  <mu-flex class="select-control-row">
    <mu-switch v-model="normal.switch" :label="'Switch: ' + normal.switch"></mu-switch>
  </mu-flex>
</mu-container>


</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"select-control-row"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.checkbox"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"'Checkbox: ' + normal.checkbox"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"select-control-group"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"select-control-row"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"'radio ' + i"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"i in 3"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"i"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.radio"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"'Radio ' + i"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"select-control-row"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.switch"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"'Switch: ' + normal.switch"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="actionscript">
export <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      normal: {
        checkbox: <span class="hljs-literal">true</span>,
        radio: <span class="hljs-number">1</span>,
        <span class="hljs-keyword">switch</span>: <span class="hljs-literal">false</span>
      }
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.select-control-row</span>{
  <span class="hljs-attribute">padding</span>: <span class="hljs-number">8px</span> <span class="hljs-number">0</span>;
}
<span class="hljs-selector-class">.select-control-group</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">16px</span> <span class="hljs-number">0</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="checkbox"><a class="header-anchor" href="#checkbox" aria-hidden="true">¶</a> Checkbox</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-checkbox v-model=\&quot;checkbox.value2\&quot; uncheck-icon=\&quot;favorite_border\&quot; checked-icon=\&quot;favorite\&quot; label=\&quot;自定义图标\&quot;&gt;&lt;/mu-checkbox&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-checkbox v-model=\&quot;checkbox.value3\&quot; uncheck-icon=\&quot;visibility_off\&quot; checked-icon=\&quot;visibility\&quot; label=\&quot;自定义图标\&quot;&gt;&lt;/mu-checkbox&gt;\n  &lt;/mu-flex&gt;\n  &lt;div class=\&quot;select-control-group\&quot;&gt;\n    Selects: {{checkbox.value1}}\n    &lt;mu-flex class=\&quot;select-control-row\&quot; :key=\&quot;'Checkbox ' + i\&quot; v-for=\&quot;i in 3\&quot;&gt;\n      &lt;mu-checkbox :value=\&quot;'Checkbox ' + i\&quot; v-model=\&quot;checkbox.value1\&quot; :label=\&quot;'Checkbox ' + i\&quot;&gt;&lt;/mu-checkbox&gt;\n    &lt;/mu-flex&gt;\n  &lt;/div&gt;\n\n  不可用状态\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-checkbox disabled label=\&quot;disabled\&quot;&gt;&lt;/mu-checkbox&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-checkbox disabled :input-value=\&quot;true\&quot; label=\&quot;disabled\&quot;&gt;&lt;/mu-checkbox&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      checkbox: {\n        value1: [],\n        value2: false,\n        value3: false\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.select-control-row{\n  padding: 8px 0;\n}\n.select-control-group {\n  margin: 16px 0;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex class="select-control-row">
    <mu-checkbox v-model="checkbox.value2" uncheck-icon="favorite_border" checked-icon="favorite" label="自定义图标"></mu-checkbox>
  </mu-flex>
  <mu-flex class="select-control-row">
    <mu-checkbox v-model="checkbox.value3" uncheck-icon="visibility_off" checked-icon="visibility" label="自定义图标"></mu-checkbox>
  </mu-flex>
  <div class="select-control-group">
    Selects: {{checkbox.value1}}
    <mu-flex class="select-control-row" :key="'Checkbox ' + i" v-for="i in 3">
      <mu-checkbox :value="'Checkbox ' + i" v-model="checkbox.value1" :label="'Checkbox ' + i"></mu-checkbox>
    </mu-flex>
  </div>

  不可用状态
  <mu-flex class="select-control-row">
    <mu-checkbox disabled label="disabled"></mu-checkbox>
  </mu-flex>
  <mu-flex class="select-control-row">
    <mu-checkbox disabled :input-value="true" label="disabled"></mu-checkbox>
  </mu-flex>
</mu-container>


</div>
                    
