      <span class="hljs-tag">&lt;<span class="hljs-name">mu-checkbox</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"'Checkbox ' + i"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkbox.value1"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"'Checkbox ' + i"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-checkbox</span>&gt;</span>
