<h2 id="switch"><a class="header-anchor" href="#switch" aria-hidden="true">¶</a> Switch</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-switch v-model=\&quot;switchVal.value1\&quot; label=\&quot;switch\&quot;&gt;&lt;/mu-switch&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-switch v-model=\&quot;switchVal.value2\&quot; label=\&quot;switch\&quot;&gt;&lt;/mu-switch&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex class=\&quot;select-control-row\&quot;&gt;\n    &lt;mu-switch v-model=\&quot;switchVal.value3\&quot; disabled label=\&quot;disabled\&quot;&gt;&lt;/mu-switch&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      switchVal: {\n        value1: false,\n        value2: true,\n        value3: false\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex class="select-control-row">
    <mu-switch v-model="switchVal.value1" label="switch"></mu-switch>
  </mu-flex>
  <mu-flex class="select-control-row">
    <mu-switch v-model="switchVal.value2" label="switch"></mu-switch>
  </mu-flex>
  <mu-flex class="select-control-row">
    <mu-switch v-model="switchVal.value3" disabled label="disabled"></mu-switch>
  </mu-flex>
</mu-container>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"select-control-row"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"switchVal.value1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"switch"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"select-control-row"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"switchVal.value2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"switch"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"select-control-row"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"switchVal.value3"</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"disabled"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">switchVal</span>: {
        <span class="hljs-attr">value1</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">value2</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">value3</span>: <span class="hljs-literal">false</span>
      }
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<p><mu-alert color="info"> 支持 input 标签所有的原生属性和事件。</mu-alert></p>
